    import {loadDataTravel} from '@/service/travels'

    export default {
        data() {
            return {
                data_table_travel: [],
                count_data_table_travel: 0,
                show_preloader_trevel: true,

                count_data_travel: 100,
                number_page_travel: 0,
            }
        },
        props: {
        },
        methods: {
            loadDataTravelFunc() {
                if (this.number_page_travel < 0) {this.number_page_travel = 0}
                this.show_preloader_trevel = false;

                loadDataTravel(this.count_data_travel).then(res => {
                   this.data_table_travel = res.data.orders
                    if (this.data_table_travel.length > 0) {
                        this.show_preloader = !this.show_preloader;
                    }

                })
                .catch(err => {
                    console.log(err)
                })
                    
            }, 
        },
        mounted() {
            this.loadDataTravelFunc()

        },
        components: {
            Header,
            Footer
        }
    }
</script>




